import axios from 'axios';

export interface Review {
  rating: number;
  text: string;
  date: string;
}

export interface Place {
  id: string;
  name: string;
  type: string;
  latitude: number;
  longitude: number;
  address?: string;
  rating?: number;
  imageUrl?: string;
  reviews?: Review[];
  distanceToMid?: number;
  distanceToLoc1?: number;
  distanceToLoc2?: number;
}

const GOOGLE_PLACES_API_KEY = process.env.REACT_APP_GOOGLE_PLACES_API_KEY;

export const searchNearbyPlaces = async (latitude: number, longitude: number, radius: number = 500): Promise<Place[]> => {
  try {
    console.log('Searching for places near:', { latitude, longitude });

    const response = await axios.get('/maps/api/place/nearbysearch/json', {
      params: {
        location: `${latitude},${longitude}`,
        radius: radius,
        type: ['restaurant', 'cafe', 'bar', 'park', 'movie_theater'].join('|'),
        key: GOOGLE_PLACES_API_KEY,
      }
    });

    if (!response.data?.results) {
      console.log('No places found in response:', response.data);
      return [];
    }

    console.log('Found places:', response.data.results.length);

    const places = await Promise.all(response.data.results
      .filter((place: any) => place.geometry && place.name)
      .map(async (place: any) => {
        let imageUrl;
        if (place.photos?.[0]?.photo_reference) {
          imageUrl = `/maps/api/place/photo?maxwidth=400&photo_reference=${place.photos[0].photo_reference}&key=${GOOGLE_PLACES_API_KEY}`;
        }

        let details;
        try {
          details = await axios.get('/maps/api/place/details/json', {
            params: {
              place_id: place.place_id,
              fields: 'review',
              key: GOOGLE_PLACES_API_KEY,
            }
          });
        } catch (error) {
          console.error('Error fetching place details:', error);
        }

        return {
          id: place.place_id,
          name: place.name,
          type: place.types?.[0]?.replace(/_/g, ' ') || 'venue',
          latitude: place.geometry.location.lat,
          longitude: place.geometry.location.lng,
          address: place.vicinity,
          rating: place.rating,
          imageUrl,
          reviews: details?.data?.result?.reviews?.map((review: any) => ({
            rating: review.rating,
            text: review.text,
            date: review.time
          }))
        };
    }));

    return places;
  } catch (error) {
    console.error('Error fetching places:', error);
    if (axios.isAxiosError(error) && error.response?.data?.error_message) {
      console.error('API Error:', error.response.data.error_message);
    }
    throw new Error('Failed to fetch nearby places. Please try again.');
  }
};